
.vux-circle {
  position: relative;
  width: 101%;
  height: 101%;
  overflow: hidden;
}
.vux-circle-content {
  width: 100%;
  text-align: center;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
}



